<template>
  <q-page class="bg-white">
    <TaskProjectOverview
      itemType="task"
      :hashId="hashId"
      :brandId="brandId"
      :itemId="itemId"
      :noTitleClick="noTitleClick"
    />
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import TaskProjectOverview from 'src/components/Requests/TaskProjectOverview.vue';

export default defineComponent({
  name: 'TaskOverview',

  components: { TaskProjectOverview },

  data() {
    return {
      title: 'Task Overview',
      noTitleClick: false,
    };
  },

  computed: {
    hashId() {
      return this.$route.hash.slice(1);
    },

    brandId() {
      return this.$route.params?.brandId;
    },

    itemId() {
      return this.$route.params?.taskId;
    },
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
  },
});
</script>
